import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { EditorService } from '../../editor.service';
import { ToolCustomiseService } from '../tool-customise.service';

@Component({
  selector: 'app-customise-rectangle',
  templateUrl: './customise-rectangle.component.html',
  styleUrls: ['./customise-rectangle.component.scss']
})
export class CustomiseRectangleComponent implements OnInit, OnDestroy {
  @Input() currentWidget: any;
  @Input() myBounds: any;
  @Input() screenType: string;
  @Input() draggable: boolean = false;
  @Input() title: string;

  public variable: string;
  public subscriptions: Subscription[] = [];

  constructor(
    private editorService: EditorService,
    private toolCustomiseService: ToolCustomiseService
  ) {
    // Event color
    this.subscriptions.push(this.toolCustomiseService.eventColor.subscribe((value: string) => {
      if (value && this.currentWidget) {
        if (this.variable === 'fill') {
          this.currentWidget.widget.config.fill = value;
        } else {
          this.currentWidget.widget.config.stroke = value;
        }
      }
    }));
  }

  ngOnInit(): void {
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((subscription) => subscription.unsubscribe());
  }

  public onClose(): void {
    this.toolCustomiseService.closeBox('customiseRectangle');
    this.editorService.focusWidgetId(null); // Close focus
  }

  public onShowSelectColorBox(variable): void {
    this.variable = variable;
    this.toolCustomiseService.showBox('selectColor', '/Shape/Select Color');
  }

  public onChangeWidget(item): void {
    this.editorService.changeWidget(item);
  }

  public toogleFill() {
    this.currentWidget.widget.config.fillEnable = !this.currentWidget.widget.config.fillEnable;
    this.onChangeWidget(this.currentWidget);
  }

  public toogleStroke() {
    this.currentWidget.widget.config.strokeEnable = !this.currentWidget.widget.config.strokeEnable;
    this.onChangeWidget(this.currentWidget);
  }

  public bringToFront(max = false){
    if(max)
      this.editorService.backupSelectedData.layerIndex = 3
    else if(Number(this.editorService.backupSelectedData.layerIndex) < 3 )
      this.editorService.backupSelectedData.layerIndex += 1
  }

  public bringToBack(max = false){
    if(max)
      this.editorService.backupSelectedData.layerIndex = 1
    else if(Number(this.editorService.backupSelectedData.layerIndex) > 1 )
      this.editorService.backupSelectedData.layerIndex -= 1
  }
}
