import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AppConfig, APP_CONFIG } from '../../app-config.module';

@Injectable({
  providedIn: 'root'
})
export class BaseService {

  constructor(@Inject(APP_CONFIG) private config: AppConfig, private httpClient: HttpClient) { }

  public get(endPoint: string, queryParams?: any): Observable<Object> {
    const httpHeaders = new HttpHeaders({ 'Content-type': 'application/json' });
    return this.httpClient.get(this.buildApiUrl(endPoint, queryParams), { headers: httpHeaders });
  }

  public post(endPoint: string, bodyParams?: any, httpHeaders?: HttpHeaders): Observable<Object> {
    return this.httpClient.post(this.buildApiUrl(endPoint), bodyParams, { headers: httpHeaders });
  }

  public patch(endPoint: string, bodyParams?: any, httpHeaders?: HttpHeaders): Observable<Object> {
    return this.httpClient.patch(this.buildApiUrl(endPoint), bodyParams, { headers: httpHeaders });
  }

  public put(endPoint: string, bodyParams?: any, httpHeaders?: HttpHeaders): Observable<Object> {
    return this.httpClient.put(this.buildApiUrl(endPoint), bodyParams, { headers: httpHeaders });
  }

  public delete(endPoint: string, bodyParams?: any): Observable<Object> {
    const httpHeaders = new HttpHeaders({ 'Content-type': 'application/json' });
    return this.httpClient.request('delete', this.buildApiUrl(endPoint), { body: bodyParams, headers: httpHeaders });
  }

  public postParams(endPoint: string, bodyParams?: any, httpHeaders?: HttpHeaders, httpParams?: HttpParams): Observable<Object> {
    return this.httpClient.post(this.buildApiUrl(endPoint), bodyParams, { headers: httpHeaders, params: httpParams });
  }

  private buildApiUrl(endPoint: string, queryParams?: object): string {
    let apiUrl = this.config.apiEndpoint;

    if (endPoint.startsWith('/')) {
      endPoint = endPoint.substr(1)
    }
    apiUrl += '/' + endPoint

    if (queryParams) {
      const queryString = this.buildQueryParam(queryParams);
      apiUrl += '?' + queryString;
    }
    return apiUrl;
  }

  public buildQueryParam(params: any) {
    let body = new HttpParams();
    if (params) {
      for (let k in params) {
        body = body.set(k, params[k]);
      }
    }
    const ps = body.toString();
    return ps;
  }
}
