import { Injectable, Inject } from '@angular/core';
import { APP_CONFIG, AppConfig } from '../../app-config.module';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ApiService {


  constructor(@Inject(APP_CONFIG) private config: AppConfig, private httpClient: HttpClient) { }

  get(url){
    let token = localStorage.getItem('token')
    let headers = {}
    if(token)
      headers = { 'Authorization': token }
    return this.httpClient.get(this.config.apiEndpoint+url,{ headers:headers })
  }

  post(url,body){
    let token = localStorage.getItem('token')
    let headers = {}
    if(token)
      headers = { 'Authorization': token }
    return this.httpClient.post(this.config.apiEndpoint+url,body,{ headers: headers })
  }
}
