// Angular
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HTTP_INTERCEPTORS } from '@angular/common/http';

// Lib
import { UserIdleModule } from 'angular-user-idle';

// Vms-admin
import { InterceptorService } from './services/interceptor.service';
import { ErrorsInterceptorService } from './services/errors-interceptor.service';
import { AutoLogoutService } from './services/auto-logout.service';

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    /**
     * Auto: logout 30 minute = (idle + timeout)
     * idle: logout 20 minute
     * timeout: reset 10 minute
     * ping: do not use
     */
    UserIdleModule.forRoot({ idle: 1200, timeout: 600, ping: 0 })
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: InterceptorService,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ErrorsInterceptorService,
      multi: true
    },
    AutoLogoutService
  ]
})
export class CoreModule { }
