// Angular
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
// NPM lib
import { UserIdleService } from 'angular-user-idle';
import { PushMessageService } from '../../shared/services/push-message.service';
// Project

@Injectable({
  providedIn: 'root'
})
export class AutoLogoutService {
  public stop(): void {
    this.userIdle.stopTimer();
  }

  public stopWatching(): void {
    this.userIdle.stopWatching();
  }

  public startWatching(): void {
    this.userIdle.startWatching();
  }

  public restart(): void {
    this.userIdle.resetTimer();
  }

  constructor(
    private router: Router,
    private userIdle: UserIdleService,
    private pushMessageService: PushMessageService,
  ) {
    // Start watching when user idle is starting.
    this.userIdle.onTimerStart().subscribe(count => {
      // console.log(count);
    });

    // Fired when time is out and id user did not stop the timer.
    this.userIdle.onTimeout().subscribe(() => {
      // Stop user idle service.
      this.userIdle.stopWatching();
      // Logout
      this.navigateLink();
    });
  }

  private navigateLink(): void {
    // Clear localstorage
    localStorage.clear();
    this.router.navigateByUrl('/login');
    this.pushMessageService.pushMessage({ title: 'Notification', content: 'Session time out. Please login again.'});
  }
}
