import {
  Component,
  OnInit
} from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ValidationErrors } from '@angular/forms';
import { ValidatorFn } from '@angular/forms';
import { AbstractControl } from '@angular/forms';

import { ApiService } from '../../shared/services/api.service';
import { PushMessageService } from '../../shared/services/push-message.service';
import { AutoLogoutService } from '../../core/services/auto-logout.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {
  emailInvalid = false;
  invalidLogin = false;
  passActive = false;
  form1 = false;
  form2 = true;
  loading = false;
  public sign1Form: FormGroup;
  public sign2Form: FormGroup;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private api: ApiService,
    private pushMessage: PushMessageService,
    private autoLogoutService: AutoLogoutService,
  ) {
    this.validate();
  }

  validate() {
    const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/;
    const passPattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).{8,}$/;
    this.sign1Form = new FormGroup({
      email: new FormControl('', [
        Validators.required,
        this.regexValidator(emailPattern, { email: 'Email is not valid' })
      ])
    });
    this.sign2Form = new FormGroup({
      password: new FormControl('', [
        Validators.required,
        this.regexValidator(passPattern, { password: 'Password is not valid' })
      ]),
    });
  }

  regexValidator(regex: RegExp, error: ValidationErrors, flag = false): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        return null;
      }
      const valid = flag ? !regex.test(control.value) : regex.test(control.value);
      return valid ? null : error;
    };
  }

  // Controls
  get f1() { return this.sign1Form.controls; }
  get f2() { return this.sign2Form.controls; }

  ngOnInit() {
    this.route.queryParams.subscribe((params) => {
      if (params.code) {
        this.loading = true
        this.api.post('users/login_playground', { code: params.code }).subscribe((res: any) => {
          this.loading = false
          if (res.token) {
            localStorage.setItem('login_type', 'email')
            localStorage.setItem('token', res.token)
            localStorage.setItem('user', JSON.stringify(res.user))
            this.router.navigate(['select-template']);
          } else {
            this.pushMessage.pushMessage({ title: 'ERROR', content: res.errors.msg })
          }
        }, (err) => {
          this.loading = false
          this.pushMessage.pushMessage({ title: 'ERROR', content: err.error.errors.msg.replace(/_/g, ' ') })
        })
      }
    })
    const params = this.route.snapshot.queryParamMap.get('type');
    this.checkStep(params);
  }

  checkEmail() {
    this.loading = true
    this.api.get('users/availability?username=' + this.sign1Form.value.email).subscribe((res: any) => {
      this.loading = false
      if (res.errors) {
        this.pushMessage.pushMessage({ title: 'ERROR', content: res.errors.msg })
      } else {
        this.form1 = !this.form1;
        this.form2 = !this.form2;
      }
    }, (err) => {
      this.loading = false
      this.pushMessage.pushMessage({ title: 'ERROR', content: err.error.errors.msg.replace(/_/g, ' ') })
    })
  }

  backStep() {
    this.form1 = !this.form1;
    this.form2 = !this.form2;
  }

  signup() {
    this.router.navigate(['signup']);
  }

  togglePwd() {
    this.passActive = !this.passActive;
  }

  forget() {
    this.router.navigate(['check-otp'])
  }

  checkStep(params: any) {
    switch (params && params.toString().trim()) {
      case 'password':
        this.emailInvalid = true;
        break;

      default:
        this.emailInvalid = false;
    }
  }

  checkLogin() {
    this.loading = true
    this.api.post('users/login', { username: this.sign1Form.value.email, password: this.sign2Form.value.password }).subscribe((res: any) => {
      this.loading = false
      if (res.token) {
        localStorage.setItem('login_type', 'email')
        localStorage.setItem('token', res.token)
        localStorage.setItem('user', JSON.stringify(res.user))
        this.router.navigate(['select-template']);
        this.startSessionTimeout();
      } else {
        this.pushMessage.pushMessage({ title: 'ERROR', content: res.errors.msg })
      }
    }, (err) => {
      this.loading = false
      this.pushMessage.pushMessage({ title: 'ERROR', content: err.error.errors.msg.replace(/_/g, ' ') })
    })
  }

  loginWithAIS() {
    this.api.get('users/login_playground').subscribe((res: any) => {
      if (res.redirectUrl)
        window.location.href = res.redirectUrl
    })
  }

  // Start SessionTimeout
  private startSessionTimeout(): void {
    this.autoLogoutService.stopWatching();
    this.autoLogoutService.startWatching();
  }
}
