import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PushMessageService {

  public message: BehaviorSubject<any> = new BehaviorSubject(null);

  constructor() { }

  public pushMessage(e) {
    this.message.next(e);
  }
}
