import { Component, OnInit, Input, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { AutoLogoutService } from '../../../core/services/auto-logout.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit {
  @Input() background: string = 'blue';
  @Input() color: string = '';
  @Input() mode = 1;
  logedIn = localStorage.getItem('token')
  user = localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : {}
  public isShowNotifications = false;

  constructor(
    private router: Router,
    private autoLogoutService: AutoLogoutService
  ) { }

  ngOnInit() {
    this.router.routeReuseStrategy.shouldReuseRoute = () => {
      return false;
    };
  }

  login() {
    this.router.navigate(['login']);
  }

  logout() {
    localStorage.clear();
    window.location.reload();
    this.autoLogoutService.stopWatching();
  }

  signup() {
    this.router.navigate(['signup']);
  }

  onReload() {
    this.router.navigateByUrl('/mysite');
  }

  onDropdown() {
    this.isShowNotifications = false;
  }

  showNotifications() {
    this.isShowNotifications = !this.isShowNotifications;
  }

  @HostListener('window:click', ['$event.target'])
  public onClickOutsite(e) {
    // Close notification
    if (this.isShowNotifications && e.className !== 'feather-bell cursor_pointer') {
      this.isShowNotifications = false;
    }
  }
}
