import { Component, OnInit } from '@angular/core';
import { Validators } from '@angular/forms';
import { ValidationErrors } from '@angular/forms';
import { AbstractControl } from '@angular/forms';
import { ValidatorFn } from '@angular/forms';
import { FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { BaseService } from '../../core/services/base.service';
import { PushMessageService } from '../../shared/services/push-message.service';

@Component({
  selector: 'app-create-project',
  templateUrl: './create-project.component.html',
  styleUrls: ['./create-project.component.scss']
})
export class CreateProjectComponent implements OnInit {

  public formGroup: FormGroup;
  public loading = false;

  constructor(
    private router: Router,
    private baseService: BaseService,
    private pushMessage: PushMessageService
  ) { }

  ngOnInit(): void {
    window.scroll(0, 0); // Init scroll to top
    this.validate();
  }

  public validate() {
    const namePattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).{1,}$/;
    this.formGroup = new FormGroup({
      name: new FormControl('', [
        Validators.required,
        this.regexValidator(namePattern, { name: 'Project name is not valid' })
      ])
    });
  }

  public regexValidator(regex: RegExp, error: ValidationErrors, flag = false): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        return null;
      }
      const valid = flag ? !regex.test(control.value) : regex.test(control.value);
      return valid ? null : error;
    };
  }

  get f() { return this.formGroup.controls; }

  public onNext() {
    this.loading = true;
    const params = {
      themeId: '',
      projectName: this.f.name.value && this.f.name.value.trim(),
      themeConfig: '',
    };
    this.baseService.post('projects', params).subscribe((res: any) => {
      if (res) {
        this.router.navigateByUrl('/editor?page=login');
      }
    }, (err) => {
      this.pushMessage.pushMessage({ title: 'ERROR', content: err.error.errors.msg.replace(/_/g, ' ') });
    });
  }

}
