import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Color } from '@iplab/ngx-color-picker';

@Component({
    selector: 'app-custom-color-picker',
    templateUrl: './custom-color-picker.component.html',
    styleUrls: ['./custom-color-picker.component.scss'],
})
export class CustomColorPickerComponent implements OnInit {
    @Input('mode') mode = 'use'
    @Input('color') color
    @Output('change') change = new EventEmitter()
    public hueColor: Color = new Color();
    public valueColor: Color = new Color();
    public presetsList = ['empty','empty','empty','empty','empty','empty','empty','empty','empty','empty','empty','empty','empty','empty'];
    // Dropdown type of color
    public typeOfColor: string = 'HEX';
    public typeOfColors: any[] = [
        { label: 'HEX', value: 'HEX' },
        { label: 'RGB', value: 'RGB' },
        { label: 'HSB', value: 'HSB' },
    ];

    constructor() {
        let json = localStorage.getItem('colorHistory') 
        if(json)
        this.presetsList = JSON.parse(json)
    }

    ngOnInit() {
        try{
            this.hueColor = new Color(this.color)
            this.valueColor = new Color(this.color)
        }catch(err){
            console.log(err)
        }
    }

    colorChange(){}

    onUse(){
        this.presetsList.pop()
        this.presetsList = [this.valueColor.toRgbaString()].concat(this.presetsList)
        localStorage.setItem('colorHistory',JSON.stringify(this.presetsList))
        this.change.emit(this.valueColor.toRgbaString())
    }

    onAdd(color){
        this.valueColor = new Color(color)
        this.change.emit(this.valueColor.toRgbaString())
    }

    onChangeTypeOfColor(e){
        console.log(e)
    }

    onSelectBase(color) {
        this.valueColor = new Color(color)
        this.hueColor = new Color(color)
        //this.change.emit(color);
    }
}
