import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ApiService } from '../../shared/services/api.service';
import { PushMessageService } from '../../shared/services/push-message.service';

@Component({
  selector: 'app-select-template',
  templateUrl: './select-template.component.html',
  styleUrls: ['./select-template.component.scss']
})
export class SelectTemplateComponent implements OnInit {
  public selectTemplate = 'all-template';
  allTemplates = []
  myTemplates = []
  constructor(
    private router: Router,
    private api: ApiService,
    private route: ActivatedRoute,
    private pushMessage: PushMessageService
  ) {
    this.route.queryParams.subscribe(params => {
      if (params['page']) {
        this.selectTemplate = params['page'];
      }
    });
  }

  ngOnInit(): void {
    this.api.get('templates').subscribe((res: any) => {
      if (res.docs)
        this.allTemplates = res.docs
    });
    this.api.get('templates/my_templates').subscribe((res: any) => {
      if (res.docs)
        this.myTemplates = res.docs
    });
  }

  public onSelectTemplate(e): void {
    this.selectTemplate = e;
  }

  public onUseThisTemplate(): void {
    this.router.navigateByUrl('create-project');
  }

  public addToMyTemplate(template): void {
    const params = { themeId: template._id };
    this.api.post(`templates/add_my`, params).subscribe((res: any) => {
      if (res) {
        // TODO
        this.selectTemplate = 'my-template';
      }
    }, (err) => {
      this.pushMessage.pushMessage({ title: 'ERROR', content: err.error.errors.msg.replace(/_/g, ' ') });
    });
  }

  setTheme(key) {
    localStorage.setItem('theme', JSON.stringify(key));
  }
}
